<template>
  <div class="energyContrast">
    <div class="search">
      <div>
        <span>能源类型</span>
        <el-select v-model="energyVal" placeholder="" @change="companyRankFun">
          <el-option
            v-for="item in energyOption"
            :key="item"
            :label="item"
            :value="item"
          >
          </el-option>
        </el-select>
      </div>
      <div style="margin-right: 0.2rem">
        <el-date-picker
          v-model="date1"
          type="date"
          placeholder=""
          value-format="yyyy-MM-dd"
          @change="companyRankFun"
          :clearable="false"
        >
        </el-date-picker>
      </div>
      <div style="margin-right: 0.2rem; color: #00b4fe">至</div>
      <div>
        <el-date-picker
          v-model="date2"
          type="date"
          placeholder=""
          value-format="yyyy-MM-dd"
          @change="companyRankFun"
          :clearable="false"
        >
        </el-date-picker>
      </div>
    </div>
    <div class="chart" ref="chart"></div>
  </div>
</template>
<script>
import { companyRank } from "@/network/energyRank";
export default {
  data() {
    return {
      energyOption: [
        "电",
        "冷凝水",
        "自来水",
        "蒸汽",
        "天然气",
        "氮气",
        "压缩空气",
        "中水"
      ],
      energyVal: "电",
      date1: "",
      date2: "",
    };
  },
  mounted() {
    this.date2 = this.date1 =
      this.$getDate().year +
      "-" +
      this.$getDate().month +
      "-" +
      this.$getDate().day;

    this.companyRankFun();
  },
  methods: {
    companyRankFun() {
      companyRank(this.energyVal, this.date1, this.date2)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            let xAxisArry = [];
            let yAxisArry = [];
            content.companyRank.sort((a, b) => {
              return b.useQty - a.useQty;
            });
            content.companyRank.forEach((item) => {
              xAxisArry.push(item.companyName);
              yAxisArry.push(item.useQty);
            });
            this.setEchart(xAxisArry, yAxisArry);
          }
        })
        .catch((err) => {});
    },
    setEchart(xAxisArry, yAxisArry) {
      let chart = this.$refs.chart;
      let myChart = this.$echarts.init(chart);
      myChart.clear();
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        grid: {
          left: "5%",
          right: "6%",
          bottom: "2%",
          top: "3%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: true,
            data: xAxisArry,
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            axisTick: {
              show: false,
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ["rgba(255, 255, 255, 0.5)"],
                type: "dashed",
              },
            },
          },
        ],
        series: [
          {
            name: "",
            type: "bar",
            z: 1,
            smooth: true,
            emphasis: {
              focus: "series",
            },
            itemStyle: {
              normal: {
                color: this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "rgba(0, 253, 152, 0.7)" }, //柱图渐变色
                  { offset: 1, color: "rgba(0, 252, 229, 0.7)" }, //柱图渐变色
                ]),
              },
            },
            data: yAxisArry,
          },
        ],
      };
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
  },
};
</script>
<style scoped lang="less">
.energyContrast {
  background-image: url(@/assets/BG2.png);
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  padding: 0.2rem;
  box-sizing: border-box;
  background-color: rgba(0, 15, 39, 1);
  width: 100%;
}
.search {
  display: flex;
  align-items: center;
  > div {
    margin-right: 0.48rem;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
  }
}
/deep/ .el-input__inner {
  background-color: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
  color: #fff;
}
//选择日期控件样式调整
/deep/ .el-input__prefix {
  right: 5px;
  left: unset;
}
/deep/ .el-input__inner {
  color: #fff;
}
/deep/ .el-input__icon {
  color: #00b4fe;
  font-size: 18px;
}
.chart {
  flex-grow: 1;
  margin-top: 0.2rem;
  border-radius: 0.05rem;
  background: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
}
</style>