import { request } from "./request";
import Vue from 'vue'

export function companyRank(energyType,fromDate,toDate) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/companyRank?areaId=" + sessionStorage.getItem('companyId') + '&energyType='+energyType+'&fromDate='+fromDate+'&toDate='+toDate,
        method: 'POST'
    })
}
